    export default {
        data() {
            return {
                infoData: null,
                loadText: '下拉加载更多',        //  呼叫中列表  下拉提示词
                loading: true,                  //   呼叫中列表   下拉圈圈
                page: 1,            //  呼叫列表请求页码页数
                maxPage: 1,
                onFetching: false,
                limit: 10,     //  一次限制请求几条数据
                value: "",   //  搜索框默认值
                pagings: 0,         // 判断选择统计槽
                todatyvalue: '',   // 日统计选择查看时间
                mothvalue: '',   // 月统计选择查看时间
                percent: null,  // 对于图形的具体填充数值
                informationList: [
                    { num: null,txt:"未打卡" },
                    { num: null,txt:"迟到" },
                    { num: null,txt:"请假" },
                    { num: null,txt:"外勤" }
                ],
                nameNum: "",
//                员工信息
                serveStaffList: [],
                trunNum: null,   //  考勤人数
                collectList: [
                    { title:"迟到",num:null },
                    { title:"早退",num:null },
                    { title:"旷工",num:null },
                    { title:"缺卡",num:null },
                    { title:"加班",num:null },
                    { title:"事假",num:null },
                    { title:"外勤",num:null }
                ],
            }
        },
        mounted(){
            this.getInfo();
        },
        methods:{
            // 初始化数据
            getInfo(){
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.bossTurn,
                    type: "post",
                    data: {

                    },
                    success: (response, allResponse) => {
                        // console.log('response',response);
                        this.todatyvalue = response.today;
                        this.infoData = response;
                        this.percent = parseInt(response.call_count/response.should_count);
                        this.informationList[0].num = response.lost_count;
                        this.informationList[1].num = response.late_count;
                        this.informationList[2].num = response.rest_count;
                        this.informationList[3].num = response.out_count;
                        this.serveStaffList = response.user_work;
                    }
                });
            },
            // 下来加载更多
            nameDown(){
                if (this.onFetching) {
                    // do nothing
                } else {
                    this.onFetching = true;
                    if(this.page > this.maxPage){
                        this.onFetching = false;
                        return;
                    }
                    this.common.commonFun.ajax({
                        dom: this,
                        url: this.common.apiUrl.workUserList,
                        type: "post",
                        data: {
                            page: this.page + 1,
                            limit: this.limit,
                        },
                        success: (response, allResponse) => {
                            // console.log('response',response.data)
                            this.onFetching = true;
                            this.maxPage = Math.ceil(response.count/this.limit) || 1;
                            if(this.page >= this.maxPage){
                                this.onFetching = false;
                                console.log('滚动结束');
                                this.loadText = "已经到底了哦~";
                                this.loading = false;
                            }
                            this.serveStaffList = this.serveStaffList.concat(response.data);
                            this.onFetching = false;
                            this.page ++;
                        }
                    });
                }
            },
            // 搜索中确定按钮
            sure(){
                console.log('我要搜索了',this.value);
                this.serveStaffList = [];
                this.page = 1;
                this.maxPage = 1;
                this.loadText = '下拉加载更多';
                if (this.onFetching) {
                    // do nothing
                } else {
                    this.onFetching = true;
                    if(this.page > this.maxPage){
                        this.onFetching = false;
                        return;
                    }
                    this.common.commonFun.ajax({
                        dom: this,
                        url: this.common.apiUrl.workUserList,
                        type: "post",
                        data: {
                            page: this.page,
                            limit: this.limit,
                            username: this.value,
                        },
                        success: (response, allResponse) => {
                            console.log('response',response.data)
                            this.onFetching = true;
                            this.maxPage = Math.ceil(response.count/this.limit) || 1;
                            if(this.page >= this.maxPage){
                                this.onFetching = false;
                                console.log('滚动结束');
                                this.loadText = "已经到底了哦~";
                                this.loading = false;
                            }
                            this.serveStaffList = this.serveStaffList.concat(response.data);
                            this.onFetching = false;
                            this.page ++;
                        }
                    });
                }
            },
            // 搜索中清除按钮
            delet(){
                this.value = '';
                this.serveStaffList = [];
                this.page = 1;
                this.maxPage = 1;
                this.loadText = '下拉加载更多';
                if (this.onFetching) {
                    // do nothing
                } else {
                    this.onFetching = true;
                    if(this.page > this.maxPage){
                        this.onFetching = false;
                        return;
                    }
                    this.common.commonFun.ajax({
                        dom: this,
                        url: this.common.apiUrl.workUserList,
                        type: "post",
                        data: {
                            page: this.page,
                            limit: this.limit,
                        },
                        success: (response, allResponse) => {
                            console.log('response',response.data);
                            this.onFetching = true;
                            this.maxPage = Math.ceil(response.count/this.limit) || 1;
                            if(this.page >= this.maxPage){
                                this.onFetching = false;
                                console.log('滚动结束');
                                this.loadText = "已经到底了哦~";
                                this.loading = false;
                            }
                            this.serveStaffList = this.serveStaffList.concat(response.data);
                            this.onFetching = false;
                            this.page ++;
                        }
                    });
                }
            },
//            头部导航箭头按钮
            backHome(){
                this.$router.push("/manage")
            },
//            头部导航右侧文字按钮
            navRigth(){
                this.$router.push("/check/detail")
            },
            // 月统计页面初始化
            monthcclearing(){
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.workUserListMonth,
                    type: "post",
                    data: {
                        date: this.mothvalue
                    },
                    success: (response, allResponse) => {
                        // console.log('response',response);
                        this.mothvalue = response.today;
                        this.trunNum = response.user_count;
                        this.collectList[0].num = response.late_count;
                        this.collectList[1].num = response.early_count;
                        this.collectList[2].num = response.lost_count;
                        this.collectList[3].num = response.miss_count;
                        this.collectList[4].num = response.over_count;
                        this.collectList[5].num = response.leave_count;
                        this.collectList[6].num = response.out_count;
                    }
                });
            },
//            分页符切换
            onItemClick (index) {
                this.pagings = index;
                if(this.pagings == 2){
                    this.$router.push('/check/turn')
                }
                if( index == 1 ){
                    this.monthcclearing();
                }
                if( index == 0 ){
                    this.getInfo();
                }
            },
//            日统计=>选择时间按钮弹窗
            onConfirm () {
                console.log("我点击了日统计=>确定按钮")
            },
//            日统计=>离开input时触发
            btnblur(){
                console.log("我要按条件搜索了")
            },
//            月统计=>选择时间按钮弹窗
            mothonConfirm(){
                // console.log(this.mothvalue+"我点击了月统计=>按钮")
                this.monthcclearing();
            },
//            进入考勤信息模块
            manage(){
                this.$router.push({
                    name: 'turnmanage',
                    params:{
                        definite: this.todatyvalue
                    }
                })
            },
//            根据不同下标请求不同的页面
            skip(index,title){
                // console.log("我点击的是第",index)
                this.$router.push({
                    path: '/check/bossturn/public',
                    name: 'public',
                    query: {
                        id: index,
                        title: title,
                    },
                    params:{
                        definite: this.mothvalue
                    }
                })
            }
        }
    }



